import React from 'react';
import styled from 'styled-components';
import { Button, Tooltip } from 'antd';
import { Link } from 'react-router-dom';
import { Course } from '../../graphql';

export const CourseHead = styled.div`
  height: 250px;
  background-size: cover !important;
  background-position: center center !important;
  overflow: hidden;
  border-radius: 5px 5px 0 0;
`;

export const CourseTitle = styled.h3`
  font-size: 24px;
  line-height: 31px;
  font-weight: 700;
  margin-bottom: 10px;
`;

export const WithPadding = styled.div`
  width: calc(100% / 3);
  padding: 12px;
  &:first-child {
    padding-left: 0;
  }
  &:nth-child(3n) {
    padding-right: 0;
  }
`;

export const Wrapper = styled.div`
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  background: #ffffff;
  height: 100%;
  border-radius: 5px;
  position: relative;
`;

const Overlay = styled(Tooltip)`
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.7);
  position: absolute;
  cursor: pointer;
  border-radius: 5px;
`;

export const CourseContent = styled.div`
  padding: 30px;
  display: flex;
  flex-direction: column;
`;

export const CourseDescription = styled.p`
  font-size: 16px;
  line-height: 24px;
`;

const getButtonText = (
  isPublished?: boolean | null,
  isFree?: boolean | null,
  isAvailable?: boolean | null,
  courseId?: string
) => {
  if (isFree) {
    return ['Начать курс', `/course/${courseId}`];
  } else {
    if (isAvailable) {
      return ['Начать курс', `/course/${courseId}`];
    }
    return ['Купить курс', `/course/preview/${courseId}`];
  }
};

interface CourseItemProps extends Course {
  isAvailable?: boolean;
  onClick?: (id: string) => void;
}

const CourseItem = ({
  id,
  title,
  description,
  isAvailable,
  onClick,
  image,
  isFree,
  isPublished
}: CourseItemProps) => {
  const [buttonText, buttonLink] = getButtonText(
    isPublished,
    isFree,
    isAvailable,
    id
  );

  return (
    <WithPadding>
      <Wrapper>
        <CourseHead
          style={{
            background: `url(${image})`
          }}
        />
        <CourseContent>
          <CourseTitle>{title}</CourseTitle>
          <CourseDescription>{description}</CourseDescription>

          <Button
            type="primary"
            onClick={onClick ? () => onClick(id) : undefined}
          >
            <Link to={buttonLink}>{buttonText}</Link>
          </Button>
        </CourseContent>
        {!isPublished ? <Overlay title="Данный курс еще не доступен!" /> : null}
      </Wrapper>
    </WithPadding>
  );
};

export default CourseItem;
